
.modal__close-button {
  position: absolute;
  top: 8px;
  right: 8px;
  color: rgba(0, 0, 0, 0.2);
  width: 30px;
  height: 30px;
  padding: 2px;
}
.modal__close-button:active, .modal__close-button:focus, .modal__close-button:hover {
    color: rgba(0, 0, 0, 0.3);
}
.modal__sponsor-button {
  display: inline-block;
  color: #cc1b00;
  background-color: rgba(255, 34, 0, 0.15);
  border-radius: 2px;
  padding: 0.75em 1.5em;
  margin-bottom: 1.2em;
}



