
.menu-entry {
  text-align: left;
  padding: 10px;
  height: auto;
  font-size: 17px;
  text-transform: none;
}
.menu-entry div div {
    text-decoration: underline;
    -webkit-text-decoration-skip: ink;
            text-decoration-skip: ink;
}
.menu-entry div div.menu-entry__sponsor {
      text-decoration: none;
}
.menu-entry > div {
    width: 100%;
}
.menu-entry span {
    display: inline-block;
    font-size: 0.75rem;
    opacity: 0.5;
    white-space: normal;
}
.menu-entry__icon {
  height: 20px;
  width: 20px;
  margin-right: 12px;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
}
.menu-entry__icon--image {
  border-radius: 2px;
  overflow: hidden;
}
.hidden-file {
  position: fixed;
  top: -999px;
}
.menu-entry__sponsor {
  float: right;
  font-size: 0.6rem;
  font-weight: 600;
  padding: 0.05em 0.25em;
  background-color: #cc1b00;
  border-radius: 3px;
  color: #fff;
}
.menu-entry__text {
  width: 100%;
}



